import { Component, OnInit, ElementRef, Input, HostBinding } from '@angular/core';
import { FUploadFileExtend } from '@farris/extend-file-upload';
import { FfilepreviewAdaptUnifileService } from './../ffilepreview-adapt-unifile.service';

@Component({
  selector: 'ffilepreview-adapt-seeimg',
  templateUrl: './ffilepreview-adapt-seeimg.component.html',
  styleUrls: ['./ffilepreview-adapt-seeimg.component.css']
})
export class FfilepreviewAdaptSeeimgComponent implements OnInit {
  @HostBinding('class.ffilepreview-adapt-seeimg') cls = true;
  // 是否启用图片的缩略图，默认为false；如果启用，图片类型会出现缩略图；不启用，根据类型显示图标
  @Input() enableThumbnail = false;
  // 图标的样式前缀
  @Input() clsPrefix = 'ffilepreview--filetype';
  // 显示缩略图中时，定义哪些后缀属于图片;
  @Input() supportImgSuffix = 'jpeg,jpg,gif,png,bmp';
  // 附件信息
  @Input() fileInfo: FUploadFileExtend;
  @Input() iconWidth=38;
  // 缩略图的最大宽度
  @Input() maxThumbnailWidth = '100%';
  // 缩略图的最小宽度
  @Input() maxThumbnailHeight = '100%';
  // 处理扩展的服务器端配置，这样可以支持每个指令预览不同的目录，比如{'rootId':'default-root','formId':'formId'}
  @Input() extendServerConfig =null;
  constructor(private adpSer: FfilepreviewAdaptUnifileService, private elementRef: ElementRef) { }

  ngOnInit() {
  }
  /**
   * 获取图片的链接
   */
  imgSrc() {
    return this.adpSer.getImgSrc(this.fileInfo, this.extendServerConfig);
  }
  /**
   * 判断是否是图片
   */
  isImage() {
    // 没有数据
    if (!this.fileInfo) {
      return false;
    }
    // 没有名称
    let fileName = this.fileInfo.name;
    if (!fileName) {
      return false;
    }
    let lastDotIndex = fileName.lastIndexOf(".");
    let fileSuffix = '';
    if (lastDotIndex > -1) {
      fileSuffix = fileName.substring(lastDotIndex + 1).toLocaleLowerCase();
    }
    if (!fileSuffix) {
      return false;
    }
    let tImgSuffixArray = this.supportImgSuffix.split(',');
    let tIndex = tImgSuffixArray.findIndex(item => {
      return item == fileSuffix;
    });
    if (tIndex > -1) {
      return true;
    }
    return false;
  }
  /**
   * 获取文件类型样式
   */
  getFileTypeClassName() {

    let clsName = this.clsPrefix;
    // 如果名称为空
    if (!this.fileInfo || !this.fileInfo['name']) {
      return clsName + '-any';
    }
    let fileName = this.fileInfo['name'];
    let lastDotIndex = fileName.lastIndexOf(".");
    let fileSuffix = '';
    if (lastDotIndex > -1) {
      fileSuffix = fileName.substring(lastDotIndex + 1).toLocaleLowerCase();
    }
    switch (fileSuffix) {
      case 'pdf':
        clsName += '-pdf';
        break;
      case 'jpeg':
      case 'jpg':
      case 'gif':
      case 'png':
      case 'bmp':
        clsName += '-img';
        break;
      case 'ppt':
        clsName += '-ppt';
        break;
      case 'doc':
      case 'docx':
        clsName += '-doc';
        break;
      case 'xls':
      case 'xlsx':
        clsName += '-xls';
        break;
      case 'txt':
        clsName += '-txt';
        break;
      case 'zip':
        clsName += '-zip';
        break;
      default:
        clsName += '-any';
    }
    return clsName;
  }
}
